/*
    @package diTheme
    ##################################################
    |   HOME PAGE STYLES                             |
    ##################################################
*/
@use "sass:math";
@import './modules/reset';
@import './modules/variables';
@import './modules/mixins';

main {
    background: white;
    bottom: 0;
    left: 0;
    overflow-x: hidden;
    overflow-y: scroll;
    perspective: 300px;
    position: absolute;
    scroll-behavior: smooth;
    top: 0;
    width: 100%;
}

.home-hero {
    height: calc(100% - 60px);
    position: relative;
    transform-style: preserve-3d;
    z-index: 0;
    @include animate(height, 1000ms);

    .bg-title {
        inset: 0;
        position: absolute;
        transform: translateZ(-1600px) scale(6.333333);

        h2 {
            color: $di__grey;
            font-size: 1.4rem;
            font-weight: 300;
            line-height: 1.6;
            padding: 0 21%;
            position: absolute;
            scroll-behavior: smooth; 
            text-align: center;
            text-transform: lowercase;
            top: 25vh;
            width: 100%;
            will-change: transform;
        }
    }

    .bg-city1 {
        inset: 0;
        position: absolute;
        transform: translateZ(-1600px) scale(6.333333);

        svg {
            bottom: 0;
            left: -65%;
            position: absolute;
            width: 350%;
            .cls-1{fill:rgb(230, 231, 232);}
            .cls-1,.cls-2,.cls-3{fill-rule:evenodd;}
            .cls-2{fill:rgb(245, 245, 246);}
            .cls-3{fill:rgb(208, 210, 211);}
        }
    }
    .bg-city2 {
        inset: 0;
        position: absolute;
        transform: translateZ(-1200px) scale(5);

        svg {
            bottom: 0;
            left: -65%;
            position: absolute;
            width: 350%;

            .cls-1{fill:rgb(223, 224, 225);}
            .cls-1,.cls-2,.cls-3{fill-rule:evenodd;}
            .cls-2{fill:rgb(245, 245, 246);}
            .cls-3{fill:rgb(208, 210, 211);}
        }
    }
    .bg-city3 {
        inset: 0;
        position: absolute;
        transform: translateZ(-900px) scale(4);

        svg {
            bottom: 0;
            left: -65%;
            position: absolute;
            width: 350%;

            .cls-1{fill:rgb(230, 231, 232);}
            .cls-1,.cls-2,.cls-3{fill-rule:evenodd;}
            .cls-2{fill:rgb(245, 245, 246);}
            .cls-3{fill:rgb(208, 210, 211);}
        }
    } 
    .bg-city4 {
        inset: 0;
        position: absolute;
        transform: translateZ(-525px) scale(2.75);

        svg {
            bottom: 0;
            left: -65%;
            position: absolute;
            width: 350%;

            .cls-1{fill:rgb(197, 199, 201);}
            .cls-1,.cls-2,.cls-3{fill-rule:evenodd;}
            .cls-2{fill:rgb(187, 189, 191);}
            .cls-3{fill:rgb(223, 224, 225);}
        }
    }
    .bg-city5 {
        inset: 0;
        position: absolute;
        top: -60px;
        transform: translateZ(-250px) scale(1.833333);

        svg {
            bottom: 0;
            fill:$di__light;
            fill-rule:evenodd;
            left: -65%;
            position: absolute;
            width: 350%;
        }
    }
    .bg-city6 {
        inset: 0;
        position: absolute;
        top: -60px;
        transform: translateZ(-125px) scale(1.4167);

        svg {
            bottom: 0;
            fill:$di__mix;
            fill-rule:evenodd;
            left: -65%;
            position: absolute;
            width: 350%;
        }
    }
    .bg-city7 {
        inset: 0;
        position: absolute;
        transform: translateZ(0px) scale(1);

        svg {
            bottom: 0px;
            fill:$di__blue;
            fill-rule:evenodd;
            left: -65%;
            position: absolute;
            width: 350%;
        }
    }
}

.home-about {
    background: $di__secondary;
    position: relative;
    top: 0px;

    .top-left {
        left: 0;
        position: absolute;
        top: 0;

        svg {
            fill: $di__blue;
            height: 40px;
        }
    }
    .top-right {
        position: absolute;
        right: 0;
        top: -39px;

        svg {
            fill: $di__secondary;
            height: 40px;
        }
    }

    h2 {
        color: $di__white;
        font-size: 1.4rem;
        font-weight: 400;
        line-height: 1.6;
        padding-top: 50px;
        position: relative;
        text-align: center;
        text-transform: lowercase;
        width: 100%;
    }

    p {
        color: $di__white;
        font-size: .725rem;
        font-weight: 400;
        padding: 5px 0 0 0;
        text-align: center;
        text-transform: lowercase;
    }

    nav {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding-top: 50px;
        position: relative;
        width: 100%;

        a {
            color: $di__white;
            display: flex;
            flex-direction: column;
            justify-content: center;
            margin-bottom: 50px;
            text-align: center;
            text-decoration: none;
            text-transform: lowercase;
            width: 50%;
            @include animate( color, 250ms );

            svg {
                fill: $di__white;
                height: 40px;
                margin-bottom: 10px;
                @include animate( fill, 250ms );
            }
            span {
                font-size: 1.2rem;
                font-weight: 300;
            }

            &:active {
                color: $di__light;
                svg { fill: $di__light; }
            }
            &:hover {
                color: $di__light;
                svg { fill: $di__light; }
            }

        }
    }
}

.home-portfolio {
    background: white;
    position: relative;

    .top-left {
        left: 0;
        position: absolute;
        top: 0;

        svg {
            fill: $di__secondary;
            height: 40px;
        }
    }
    .top-right {
        position: absolute;
        right: 0;
        top: -39px;

        svg {
            fill: white;
            height: 40px;
        }
    }

    h2 {
        color: $di__blue;
        font-size: 1.4rem;
        font-weight: 400;
        line-height: 1.6;
        padding-top: 50px;
        position: relative;
        text-align: center;
        text-transform: lowercase;
        width: 100%;
    }

    p {
        color: $di__grey;
        font-size: .725rem;
        font-weight: 400;
        padding: 5px 0 40px 0;
        text-align: center;
        text-transform: lowercase;
    }

    .portfolio-carousel {
        align-items: center;
        box-sizing: content-box;
        display: flex;
        height: 240px;
        justify-content: space-between;
        padding-bottom: 80px;
        position: relative;
        transform-style: preserve-3d;

        button {
            background: none;
            border: none;
            outline: none;
            padding: 0;
            z-index: 3;

            svg {
                fill: none;
                height: 30px;
                stroke: $di__grey;
                stroke-width: 6px;
                @include animate( stroke, 250ms );
            }

            &:active {
                svg {
                    stroke: $di__light;
                }
            }
            &:hover {
                svg {
                    stroke: $di__light;
                }
            }
        }

        .card {
            align-items: center;
            background: white;
            border: solid 1px $di__grey-light;
            box-shadow: 0 0 3px rgba(0, 0, 0, .05);
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            left: calc(50% - 150px / 2);
            opacity: 1;
            position: absolute;
            text-align: center;
            width: 150px;
            z-index: 3;
            @include animate( all, 500ms );

            .card-img {
                height: 150px;
                position: relative;
                width: 100%;

                img {
                    height: 100%;
                    left: 0;
                    object-fit: cover;
                    object-position: top left;
                    overflow: hidden;
                    position: absolute;
                    top: 0;
                    width: 100%;
                }

                svg#di_logo {
                    bottom: 0;
                    left: 0;
                    object-fit: cover;
                    position: absolute;
                    top: 0;
    
                    .cls-2{fill: $di__tertiary;}
                    .cls-3{fill: $di__mix;}
                    .cls-4{fill: $di__light;}
                    .cls-5{fill: $di__blue;}
                    .cls-6{fill: $di__dark;}
                }
            }

            .card-info {
                display: flex;
                flex-direction: column;
                line-height: 1.2;
                padding: 1rem;
                position: relative;
                text-align: center;
                width: 100%;
                @include animate( opacity, 300ms );
    
                h4 {
                    color: $di__primary;
                    font-size: 1.1rem;
                    font-weight: 700;
                    padding: .5rem 1rem 0;
                    position: relative;
                    text-transform: uppercase;
                }
                span {
                    color: $di__grey;
                    font-size: .9rem;
                    font-weight: 400;
                    padding: .5rem 1rem 0;
                    position: relative;
                    text-transform: lowercase;
                }

                a.project-link {
                    background: $di__secondary;
                    border: solid 1px $di__primary;
                    border-radius: 1.5rem;
                    color: white;
                    margin: 1rem .5rem;
                    padding: 1rem .5rem;
                    text-align: center;
                    text-decoration: none;
                    text-transform: lowercase;
                }
            }

            &.back {
                opacity: .3;
                transform: scale(.5);
                z-index: 1;
                @include animate( all, 500ms );
            }

            &.left {
                opacity: .3;
                transform: translateX(-70px) scale(.75);
                z-index: 2;
            }

            &.right {
                opacity: .3;
                transform: translateX(70px) scale(.75);
                z-index: 2;
            }

            &.hidden {
                opacity: 0;
                @include animate( all, 500ms );
            }
        }
        
    }
}

