.home-testimonials {
    background: white;
    padding-bottom: 50px;
    position: relative;

    .top-left {
        left: 0;
        position: absolute;
        top: 0;

        svg {
            fill: $di__grey-light;
            height: 40px;
        }
    }
    .top-right {
        position: absolute;
        right: 0;
        top: -39px;

        svg {
            fill: white;
            height: 40px;
        }
    }

    h2 {
        color: $di__blue;
        font-size: 1.4rem;
        font-weight: 400;
        line-height: 1.6;
        padding-top: 50px;
        position: relative;
        text-align: center;
        text-transform: lowercase;
        width: 100%;

    }

    p {
        color: $di__grey;
        font-size: .7rem;
        font-weight: 400;
        line-height: 1.3;
        padding: 5px 0 40px 0;
        text-align: center;
        text-transform: lowercase;
    }

    .testimonial-slider {
        align-items: center;
        display: flex;
        justify-content: space-between;
        margin-top: 40px;
        padding-bottom: 40px;
        position: relative;

        button {
            background: none;
            border: none;
            outline: none;
            padding: 0;
            z-index: 3;

            svg {
                fill: none;
                height: 30px;
                stroke: $di__grey;
                stroke-width: 6px;
                @include animate( stroke, 250ms );
            }

            &.disabled {
                cursor: default;

                svg {
                    stroke: $di__grey-light;
                }

                &:active {
                    svg {
                        stroke: $di__grey-light;
                    }
                }
                &:hover {
                    svg {
                        stroke: $di__grey-light;
                    }
                }
            }

            &:active {
                svg {
                    stroke: $di__light;
                }
            }
            &:hover {
                svg {
                    stroke: $di__light;
                }
            }
        }

        figure {
            align-items: center;
            background: white;
            color: $di__grey;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;
            left: 5%;
            opacity: 1;
            padding: 0 10px;
            position: absolute;
            text-align: center;
            width: 90%;
            @include animate( transform, 500ms );

            svg {
                align-self: flex-start;
                fill: $di__light;
                height: 15px;
            }
            
            blockquote {
                flex: 1;
                margin: 0;
                padding: 0 10px;

                p {
                    font-size: .75rem;
                    font-style: italic;
                    font-weight: 400;
                    padding: 7px 0 15px 0;
                    text-transform: unset;
                }
            }

            figcaption {
                display: flex;
                flex: 1 100%;
                flex-direction: column;
                width: 100%;

                span {
                    color: $di__blue;
                    font-size: 1rem;
                    font-weight: 600;
                    position: relative;
                }
                cite {
                    font-size: .8rem;
                    font-style: normal;
                    font-weight: 400;
                    padding-top: 5px;
                }
            }

            &.left {
                transform: translateX(-100vw);
            }

            &.right {
                transform: translateX(100vw);
            }

        }

    }
}

